.modal {
    padding: calc(40px / 2);
    position: fixed;
    width: calc(100% - calc(40px));
    height: 75vh;
    background-color: var(--block2);
    bottom: 0;
    border-radius: 15px 15px 0 0;
    z-index: 10;
    transition-duration: 200ms;
    display: flex;
    flex-direction: column;
    row-gap: 10px;

    &_add {
        height: calc(100vh - calc(var(--margin-display) * 2));
        border-radius: 0;
    }

    &_top {
        width: 100%;
        flex-shrink: 0;
        display: flex;
        justify-content: space-between;
        align-items: center;

        &_p1 {
            font-family: var(--text-3-family);
            font-size: var(--text-3-size);
            font-weight: var(--text-3-weight);
            color: var(--text-3-color);

        }

        &_cross {
            height: 100%;
            aspect-ratio: 1/1;
            display: flex;
            justify-content: center;
            align-items: center;
        }

        &_input {
            width: 75%;
            padding-left: 10px;
            height: 35px;
            border-radius: 12px;
            align-self: self-start;
            background-color: var(--block3);
            border: none;
            outline: none;
            font-family: var(--text-4-family);
            font-size: var(--text-4-size);
            font-weight: var(--text-4-weight);
            color: var(--text-4-color);

            &::placeholder {
                font-family: var(--text-2-family);
                font-size: var(--text-2-size);
                font-weight: var(--text-2-weight);
                color: var(--text-2-color);
            }
        }
    }

    &_place {
        overflow: scroll;
        display: flex;
        flex-direction: column;
        row-gap: 10px;
    }

    &_content {
        width: 100%;
        margin-top: 15px;
        display: flex;
        justify-content: space-between;
        column-gap: 10px;

        img {
            border-radius: 3px;
        }

        &_info {
            width: 100%;
            height: 99px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;

            &_p1 {
                font-family: var(--text-2-family);
                font-size: var(--text-2-size);
                font-weight: var(--text-2-weight);
                color: var(--text-2-color);
            }

            &_panel {
                display: flex;
                justify-content: space-between;
                align-items: center;
                height: 25px;

                &_p1 {
                    font-family: var(--text-4-family);
                    font-size: var(--text-4-size);
                    font-weight: var(--text-4-weight);
                    color: var(--text-4-color);
                }
            }
        }
    }

    &_line {
        width: 100%;
        height: 1px;
        background-color: #ffffff2c;
        margin: 15px 0;
    }

    &_text {
        width: 85%;
        font-family: var(--text-2-family);
        font-size: var(--text-2-size);
        font-weight: var(--text-2-weight);
        color: var(--text-2-color);
        margin-top: 12px;
    }

    &_table {
        width: 100%;
        background-color: var(--block1);

        &_item {
            width: 100%;
            height: 25px;
            border-bottom: 1px solid var(--block2);
            display: flex;

            &_p1 {
                height: 100%;
                border-right: 1px solid var(--block2);
                font-family: var(--text-4-family);
                font-size: var(--text-4-size);
                font-weight: var(--text-4-weight);
                color: var(--text-4-color);
                width: calc(50% - 10px);
                padding-left: 8px;
                display: flex;
                align-items: center;
            }

            &_p2 {
                width: calc(50% - 10px);
                padding-left: 8px;
                display: flex;
                align-items: center;
                height: 100%;
                font-family: var(--text-2-family);
                font-size: var(--text-2-size);
                font-weight: var(--text-2-weight);
                color: var(--text-2-color);
            }
        }
    }
}

.massage {
    width: 100%;
    height: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: var(--text-4-family);
    font-size: var(--text-4-size);
    font-weight: var(--text-4-weight);
    color: var(--text-4-color);
    text-decoration: underline;
}