.content {
    width: calc(100% - calc(var(--margin-display)));
    height: auto;
    background-color: var(--block);
    padding: 0 calc(var(--margin-display)/2);


    &_title {
        width: 100%;
        height: 60px;
        border-bottom: 1px solid var(--block-light);
        display: flex;
        justify-content: space-between;

        align-items: center;

        &_com {
            display: flex;
            height: 100%;
            column-gap: 25px;
            align-items: center;

            &_menu {
                margin-left: calc(var(--margin-display) / 2);
                display: flex;
                justify-content: center;
                flex-direction: column;

                &_p1 {
                    font-family: var(--text-1-family);
                    font-size: var(--text-1-size);
                    font-weight: var(--text-1-weight);
                    color: var(--text-1-color);
                }

                &_line {
                    width: 120%;
                    height: 1px;
                    background-color: #fff;
                }
            }

            &_search {
                padding: 0 15px 0 15px;
                height: 60%;
                background-color: var(--block2);
                display: flex;
                align-items: center;
                border-radius: 15px;
                font-family: var(--text-2-family);
                font-size: var(--text-2-size);
                font-weight: var(--text-2-weight);
                color: var(--text-2-color);
                cursor: pointer;
                user-select: none;
                outline: none;
                -webkit-tap-highlight-color: transparent;
            }
        }

        &_text {
            font-family: var(--text-2-family);
            font-size: var(--text-2-size);
            font-weight: var(--text-2-weight);
            color: var(--text-2-color);

            width: 55%;
            text-align: end;

        }
    }
}

.background {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: #000000a6;
    backdrop-filter: blur(1px);
    z-index: 4;
}