.back {
    position: fixed;
    top: -100px;
    left: 0;
    width: 100%;
    height: calc(100vh + 100px);
    background: url(../../../features/images/img/background.jpg);
    z-index: -1;
    background-size: cover;
    background-repeat: no-repeat;
}

.background {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: #000000a6;
    backdrop-filter: blur(1px);
    z-index: 4;
}