.modal-enter {
    transform: translateY(100%);
}

.modal-enter-active {
    transition: all 300ms ease-out;
    transform: translateY(0);
}

.modal-exit {
    transform: translateY(0);
}

.modal-exit-active {
    transform: translateY(100%);
    transition: all 400ms ease;
}


.opacity-enter {
    opacity: 0;
}

.opacity-enter-active {
    opacity: 1;
    transition: all 400ms ease-out;
}

.opacity-exit {
    opacity: 1;
}

.opacity-exit-active {
    opacity: 0;
    transition: all 400ms ease;
}





.like-enter {}

.like-enter-active {}

.like-exit {
    max-height: 50px;
    opacity: 1;
    transform: scale(1);
}

.like-exit-active {
    transform: scale(0);
    margin-bottom: -25px;
    max-height: 0px;
    opacity: 0;
    transition-duration: 1s;
}